/**
 * ui.js
 * Read input from index.html
 * Show output from mccabe-thiele.js to index.html
 */

export class Variable {
  xd;
  xb;
  zf;
  q;
  r_factor;
  nm;
  sc;

  constructor() {
    // Used parseFloat() for shifting string(.value) to number
    //@ts-ignore
    this.xd = parseFloat(document.getElementById("xd").value);
    //@ts-ignore
    this.xb = parseFloat(document.getElementById("xb").value);
    //@ts-ignore
    this.zf = parseFloat(document.getElementById("zf").value);
    //@ts-ignore
    this.q = parseFloat(document.getElementById("q").value);
    //@ts-ignore
    this.r_factor = parseFloat(document.getElementById("r_factor").value);
    //@ts-ignore
    this.nm = parseFloat(document.getElementById("nm").value);
    //@ts-ignore
    this.sc = parseFloat(document.getElementById("sub_cooling").value);
    this.validate();
  }

  /**
   * @returns {boolean} - 모든 값이 유효하면 true, 아니면 false
   */
  validate() {
    const allValues = [
      this.xd,
      this.xb,
      this.zf,
      this.q,
      this.r_factor,
      this.nm,
      this.sc,
    ];
    if (allValues.some((val) => isNaN(val))) {
      console.error("입력값 오류: 일부 값이 숫자가 아닙니다 (NaN).", this);
      alert("유효하지 않은 입력값이 있습니다. 숫자만 입력해주세요.");
      return false;
    }
    if (this.xd <= this.zf || this.zf <= this.xb) {
      console.warn(
        `입력값 경고: 조성 순서가 올바르지 않습니다 (xd > zf > xb 여야 함). xd=${this.xd}, zf=${this.zf}, xb=${this.xb}`,
      );
    }
    return true;
  }
}

// [DOM 요소 캐싱]
// Canvas에서 이미 선언된 요소들을 여기에 추가적으로 캐싱 (main.js에서도 사용됨)
const systemSelect = document.getElementById('system_select');
const systemDescription = document.getElementById('system-description');
const vleInputModal = document.getElementById('vle-input-modal');
const addVleRowBtn = document.getElementById('add-vle-row-btn');
const vleDataList = document.getElementById('vle-data-list');
const saveVleBtn = document.getElementById('save-vle-btn');
const cancelVleBtn = document.getElementById('cancel-vle-btn');

// [전역 상태]
// 사용자가 입력한 VLE 데이터를 임시로 저장할 배열 (나중에 DB나 LocalStorage로 옮겨야 함)
let customVLEData = []; 


/**
 * 페이지 로드 시 UI 이벤트를 설정합니다.
 */
export function setUpUIListeners() {
    setUpSystemSelection();
    setUpVLEModalManagement();
    setUpDynamicVLEInput();
}


// --- VLE 시스템 선택 및 모달 제어 로직 ---

/**
 * System selection 드롭다운 리스너를 설정합니다.
 */
function setUpSystemSelection() {
    //if (!systemSelect || !vleInputModal) return;
    systemSelect.addEventListener('change', (event) => {
        // @ts-ignore
        const selectedValue = event.target.value;
        if (selectedValue === 'methanol_water') {
            systemDescription.innerHTML = '<b>System:</b> Methanol-Water @ 1 atm (VLE 데이터 기반)';
            // [TODO] VLE 데이터 상태를 Default로 리셋하는 로직 추가
        } else if (selectedValue === 'user_input') {
            // "Add Custom VLE Data..."를 선택하면 모달을 띄웁니다.
            showVLEModal();
        }
    });
}

/**
 * VLE 데이터 입력 모달의 버튼 리스너를 설정합니다.
 */
function setUpVLEModalManagement() {
    if (cancelVleBtn) {
        cancelVleBtn.addEventListener('click', () => {
            hideVLEModal();
            // 취소 시 드롭다운을 Default로 되돌립니다.
            //@ts-ignore
            if (systemSelect) systemSelect.value = 'methanol_water'; 
        });
    }
    if (saveVleBtn) {
        saveVleBtn.addEventListener('click', handleVLEDataSave);
    }
}

/**
 * VLE 입력 모달을 표시합니다.
 */
function showVLEModal() {
    if (vleInputModal) {
        vleInputModal.classList.remove('hidden');
        // 모달을 열 때마다 초기 입력 행을 유지하고 나머지 데이터는 비우는 것이 좋음
        clearVLERows(); 
    }
}

/**
 * VLE 입력 모달을 숨깁니다.
 */
function hideVLEModal() {
    if (vleInputModal) {
        vleInputModal.classList.add('hidden');
    }
}


// --- 동적 VLE 데이터 행 관리 로직 ---

/**
 * VLE 데이터 입력 행의 추가/제거 리스너를 설정합니다.
 */
function setUpDynamicVLEInput() {
    if (addVleRowBtn) {
        addVleRowBtn.addEventListener('click', () => {
            addVLEDataRow();
        });
    }
    // 부모 요소에 리스너를 달아 동적으로 생성된 '제거' 버튼을 처리 (이벤트 위임)
    if (vleDataList) {
        vleDataList.addEventListener('click', (event) => {
            // @ts-ignore
            if (event.target.classList.contains('remove-vle-row')) {
                // @ts-ignore
                event.target.closest('.vle-row').remove();
                updateRemoveButtonVisibility();
            }
        });
    }
}

/**
 * 새로운 VLE 데이터 입력 행을 동적으로 추가합니다.
 */
function addVLEDataRow(x = 0.5, y = 0.5) {
    if (!vleDataList) return;
    
    const row = document.createElement('div');
    row.className = 'vle-row';
    
    row.innerHTML = `
        <label>x (Liquid):</label>
        <input type="number" step="any" min="0" max="1" value="${x}" class="vle-x" required>
        <label>y (Vapor):</label>
        <input type="number" step="any" min="0" max="1" value="${y}" class="vle-y" required>
        <button type="button" class="remove-vle-row">-</button>
    `;
    
    // [핵심 수정] 새 행을 마지막 행 (index: totalRows - 1) '앞'에 삽입합니다.
    const rows = vleDataList.querySelectorAll('.vle-row');
    const lastRow = rows.length > 0 ? rows[rows.length - 1] : null;

    if (lastRow) {
        // 마지막 행이 있다면, 그 앞에 새 행을 삽입합니다. (1.0 행 앞)
        vleDataList.insertBefore(row, lastRow);
    } else {
        // 행이 없다면 (비정상적인 경우), 그냥 끝에 추가합니다.
        vleDataList.appendChild(row);
    }

    updateRemoveButtonVisibility();
}

/**
 * VLE 데이터 행들을 모두 비우거나, 초기 1개만 남깁니다.
 * [수정] 0,0 행과 1,1 행을 항상 생성 및 초기화합니다.
 */
function clearVLERows() {
    //let rows = vleDataList.querySelectorAll('.vle-row');
    
    // 1. 모든 행 삭제
    vleDataList.innerHTML = '';

    // 2. [핵심] 경계값 행 (0,0 및 1,1)을 항상 생성합니다.
    // 0,0 행 (첫 행)
    const firstRow = document.createElement('div');
    firstRow.className = 'vle-row';
    firstRow.innerHTML = `
        <label>x (Liquid):</label>
        <input type="number" step="any" min="0" max="1" value="0.0" class="vle-x" required>
        <label>y (Vapor):</label>
        <input type="number" step="any" min="0" max="1" value="0.0" class="vle-y" required>
        <button type="button" class="remove-vle-row" style="visibility: hidden;">-</button>
    `;
    vleDataList.appendChild(firstRow);
    
    // 1,1 행 (마지막 행)
    const lastRow = document.createElement('div');
    lastRow.className = 'vle-row';
    lastRow.innerHTML = `
        <label>x (Liquid):</label>
        <input type="number" step="any" min="0" max="1" value="1.0" class="vle-x" required>
        <label>y (Vapor):</label>
        <input type="number" step="any" min="0" max="1" value="1.0" class="vle-y" required>
        <button type="button" class="remove-vle-row" style="visibility: hidden;">-</button>
    `;
    vleDataList.appendChild(lastRow);
    
    // 3. KeepFirstRow 로직 (여기서는 무시하고, 단순히 0,0과 1,1만 유지)
    // 이전 clearVLERows의 복잡한 로직이 필요 없게 됨.
    
    updateRemoveButtonVisibility();
}

/**
 * VLE 데이터 행이 1개일 때만 제거 버튼을 숨깁니다.
 */
function updateRemoveButtonVisibility() {
    if (!vleDataList) return;
    const rows = Array.from(vleDataList.querySelectorAll('.vle-row')); 
    const totalRows = rows.length;

    rows.forEach((row, index) => { // [수정] 인덱스를 활용
        const removeBtn = row.querySelector('.remove-vle-row');
        if (removeBtn) {
            // [핵심 로직] 첫 행(index === 0)과 마지막 행(index === totalRows - 1)이 아니면 visible
            const isMiddleRow = index > 0 && index < totalRows - 1;
            // @ts-ignore
            removeBtn.style.visibility = isMiddleRow ? 'visible' : 'hidden';
        }
    });
}


// --- VLE 데이터 저장 및 유효성 검사 ---

/**
 * 'Save & Use VLE Data' 버튼 클릭 시 호출됩니다.
 */
function handleVLEDataSave() {
    if (!vleDataList) return;

    const data = [];
    const rows = vleDataList.querySelectorAll('.vle-row');
    
    // 1. 데이터 추출 (유효성 검사 없이 순수 데이터만 추출)
    for (const row of rows) {
        // @ts-ignore
        const xInput = row.querySelector('.vle-x');
        // @ts-ignore
        const yInput = row.querySelector('.vle-y');

        // [수정] Number() 대신 parseFloat() 사용 (NaN 체크는 validateVLEData에서 처리)
        //@ts-ignore
        const x = parseFloat(xInput.value);
        //@ts-ignore
        const y = parseFloat(yInput.value);

        data.push({ x: x, y: y });
    }

    // 2. [핵심] 최종 유효성 검사
    if (!validateVLEData(data)) {
        return; 
    }
    
    // 3. [TODO] DB 또는 LocalStorage에 저장하는 로직
    customVLEData = data;
    
    // 4. UI 업데이트
    hideVLEModal();
    alert(`Success: ${data.length} Custom VLE data points have been saved and will be used for calculation.`);

    // [TODO] VLE 시스템 설명을 사용자 정의 이름으로 업데이트
    const compA = document.getElementById('comp_name_A');
    const compB = document.getElementById('comp_name_B');
    // @ts-ignore
    const systemName = (compA && compB) ? `${compA.value}-${compB.value}` : 'Custom System';

    if (systemDescription) {
         systemDescription.innerHTML = `<b>System:</b> ${systemName} (Custom VLE) <br><b>Assumptions:</b> Total Condenser, Partial Reboiler`;
    }
}

/**
 * [TODO] 나중에 main.js/mccabe-thiele.js가 이 함수를 호출하여 데이터를 가져갈 것입니다.
 * @returns {Array<{x: number, y: number}>}
 */
export function getCustomVLEData() {
    return customVLEData;
}

/**
 * [신규] VLE 최종 제출 시 세 가지 핵심 유효성 검사를 수행합니다.
 * @param {Array<{x: number, y: number}>} data - VLE 데이터 배열
 * @returns {boolean} - 유효성 검사 통과 여부
 */
function validateVLEData(data) {
    // 0. [신규 검사] 요소 이름 및 출처 비어 있는지 확인 (필수)
    const compAInput = document.getElementById('comp_name_A');
    const compBInput = document.getElementById('comp_name_B');
    const sourceInput = document.getElementById('vle_source_input');
    
    // [수정] trim() 후 값이 없으면 오류
    // @ts-ignore
    if (!compAInput || !compAInput.value || compAInput.value.trim() === '') {
        alert("Validation Error: Component A name cannot be empty. Please fill in the component name.");
        // @ts-ignore
        compAInput.focus();
        return false;
    }
    // @ts-ignore
    if (!compBInput || !compBInput.value || compBInput.value.trim() === '') {
        alert("Validation Error: Component B name cannot be empty. Please fill in the component name.");
        // @ts-ignore
        compBInput.focus();
        return false;
    }
    // @ts-ignore
    if (!sourceInput || !sourceInput.value || sourceInput.value.trim() === '') {
        alert("Validation Error: Source/Remarks field cannot be empty. Please provide a source, link, or brief remark.");
        // @ts-ignore
        sourceInput.focus();
        return false;
    }

    // 0.5 [신규 검사] 숫자 유효성 검사 (NaN 체크)
    const invalidNumber = data.find(p => isNaN(p.x) || isNaN(p.y));
    if (invalidNumber) {
         alert("Validation Error: Invalid number input. Please ensure all x and y fields contain valid numerical data.");
         return false;
    }


    // 1. [검사] Out of Range 검사 (0보다 작거나 1보다 큰지)
    const outOfRange = data.find(p => p.x < 0 || p.x > 1 || p.y < 0 || p.y > 1);
    if (outOfRange) {
        alert(`Validation Error: Out of range input. Both x (${outOfRange.x.toFixed(3)}) and y (${outOfRange.y.toFixed(3)}) components must be between 0 and 1.`);
        return false;
    }

    // 2. [검사] 경계값 (Boundary Check): 첫 행 (0,0) 및 마지막 행 (1,1) 검사
    const firstPoint = data[0];
    const lastPoint = data[data.length - 1];
    
    if (Math.abs(firstPoint.x) > 1e-10 || Math.abs(firstPoint.y) > 1e-10 || 
        Math.abs(lastPoint.x - 1.0) > 1e-10 || Math.abs(lastPoint.y - 1.0) > 1e-10) {
        alert("Validation Error: Invalid boundary points. The first point must be (0.0, 0.0) and the last point must be (1.0, 1.0). (Floating point precision error detected)");
        return false;
    }
    
    // 3. [검사] X값 중복 검사 및 정렬 검사 (중복은 invalid X component를 의미)
    const xValues = data.map(p => p.x);
    const sortedXValues = [...xValues].sort((a, b) => a - b);
    
    for (let i = 0; i < xValues.length; i++) {
        // [중복 검사]
        if (i > 0 && Math.abs(xValues[i] - xValues[i - 1]) < 1e-10) { // 부동소수점 오차 허용
            alert(`Validation Error: Invalid x components. Duplicate liquid composition (x = ${xValues[i].toFixed(3)}) found. VLE data must have unique x values.`);
            return false;
        }
        
        // [정렬 확인] (부동소수점 오차 허용)
        if (Math.abs(xValues[i] - sortedXValues[i]) > 1e-10) {
            alert("Validation Error: VLE data must be sorted by the liquid composition (x value). Please ensure your x values are strictly increasing.");
            return false;
        }
    }

    // 4. [신규 검사] 최소 데이터 포인트 수 (경고만)
    if (data.length <= 5) {
        alert(`Warning: The number of data points (${data.length}) is very low. VLE curve accuracy may be compromised. Using more data points (e.g., > 10) is recommended.`);
    }

    return true; // 모든 검사 통과
}

// 초기화 호출 (main.js에서 이 모듈을 import할 때 실행됨)
setUpUIListeners();